<template>
    <div class="classify">
        <div>
            <van-nav-bar title="分类"  class="van-head">
                <template #left>
                    <div class="fl title-logo"></div>
                </template>
                <template #right>
                    <span class="iconfont icon-shujia van-head-icon" @click="toggleTabBar('Bookshelf')"></span>
                    <van-icon name="wap-home" size="20" @click="toggleTabBar('Bookmall')"></van-icon>
                </template>
            </van-nav-bar>
        </div>

        <div class="classify-box">
            <div class="classify-box-item"  v-for="(item,index) in classifyData" :key="index">
                <div class="box-item-title">{{item.title}}</div>
                <div class="classify-book-type clearfix">
                    <div class="box-book fl" :style="{backgroundImage:'url('+item.icon+')'}"  v-for="(types,index2) in productData[item.name]" :key="index2" @click="skipId(item.name,types.name)">
                        <div class="box-book-title">{{types.name}}</div>
                        <div class="box-book-number">{{types.bookCount}}</div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import '../../assets/css/classify.less'
export default {

    data(){
        return{
            classifyData:[
                {
                    title:'男生',
                    name:'male'
                },
                {
                    title:'女生',
                    name:'female'
                },
                {
                    title:'出版',
                    name: 'press'
                }
            ],
            //数据
            productData:{}
        }
    },

    created(){
        //获取数据
        this.getProductData();
    },
    methods:{
        //路由跳转
        toggleTabBar(name){
            
            this.$router.push({name:name});
        },
        //数据类型跳转
        skipId(gender,type){
            
            this.$router.push({name:'Category',query:{
                gender:gender,
                type:type
            }});
        },
        
        getProductData(){

            
            this.$toast.loading({
                message: '加载中...',
                forbidClick: true,
                duration: 0
            });

            
            this.axios({
                method:'GET',
                url:'http://novel.kele8.cn/categories',
                params:{
                    appkey: this.appkey
                }
            }).then(result => {
                this.$toast.clear();
                if(result.data){

                    this.productData = result.data;
                }
               
            }).catch(err => {
                this.$toast.clear();
            })
        },

    }
    
}
</script>